@-webkit-keyframes typing {
    from { width: 0 }
    to { width:100% }
}

@-moz-keyframes typing {
    from { width: 0 }
    to { width:100% }
}

@-webkit-keyframes blink-caret {
    from, to { border-color: transparent }
    50% { border-color: black }
}

@-moz-keyframes blink-caret {
    from, to { border-color: transparent }
    50% { border-color: black }
}


/*types are for pure CSS typing animations*/
.type1 p {
	color: #fff;
	overflow: hidden; /* Ensures the content is not revealed until the animation */
	white-space: nowrap; /* Keeps the content on a single line */
	margin: 0 auto; /* Gives that scrolling effect as the typing happens */
	letter-spacing: .15em; /* Adjust as needed */
	border-right: 2px solid black;
	animation: typing 1s steps(45, end), blink-caret .5s step-end infinite;
	padding-bottom: 0;
}

.type1{
	width: 33%;
    margin: 0 auto;
    padding-bottom: 40px;
}

.type2 { 
    /*width:16.3em;*/
    white-space:nowrap;
    overflow:hidden;
    /*border-right: .1em solid black;*/
    
    -webkit-animation: typing 2s steps(19, end), /* # of steps = # of characters */
                        blink-caret 1s step-end infinite;
    -moz-animation: typing 2s steps(19, end), /* # of steps = # of characters */
                        blink-caret 1s step-end infinite;
}
/*types are for pure CSS typing animations*/

.hidden-arrow {
	display: none;
}

.visible-arrow {
	display: inline-block;

}

.bigger {
	transition: all 300ms ease-in;
    transform: scale(1.2);
}

.typed-cursor {
    -webkit-animation: blinker .5s linear infinite;
            animation: blinker .5s linear infinite;
}

@-webkit-keyframes blinker {
    50% {
        opacity: 0;
        -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=0)";
    }
}

@keyframes blinker {
    50% {
        opacity: 0;
        -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=0)";
    }
}

