.overlay {
	.project-description {
		display: none;
	}
	&:hover {
		opacity: 1;
		transition: opacity 500ms ease;
		img {
			opacity: 0.35;
			transition: opacity 500ms ease;
		}

		.project-description {
			display: inline-block;
			font-weight: bold;
			font-size: 2rem;
			float: left;
			color: #000;
			position: relative;
			bottom: 12rem;
			left: 12.5rem;
			transition: opacity 500ms ease;
		}
	}

}

.contact-overlay {

	&:hover {
		.vira-card p {
			opacity: 0.35;
			transition: opacity 500ms ease;
		}

		h3 {
			opacity: 1;
			transform: scale(1.5);
			transition: transform 500ms ease;
		}
		.fa {
			transform: scale(1.5);
			transition: transform 500ms ease;
		}
	}
}

