body {
//   background-color:#1D1D1D;
//   min-height:100vh;
//   margin:0;
//   font-family: 'Droid Sans', sans-serif;
  &:before {
    content: '';
    position: fixed;
    top:0px;
	// top:212px;
    left:50%;
    bottom:150px;
    transform:translateX(-50%);
    width:4px;
    background-color:#fff;
	z-index: 0;
  }
  .entries {
    width:calc(100% - 80px);
    max-width:800px;
    margin:auto;
    position: relative;
    left:-5px;
	z-index: 0;
    .entry {
      width:calc(50% - 80px);
      float:left; 
      padding:20px;
      clear:both;
      text-align:right;
      &:not(:first-child) {
        margin-top:-60px;
      }
      .title {
        font-size:32px;
        margin-bottom:12px;
        position: relative;
        color:#fff;
        &:before {
          content: '';
          position: absolute;
          width:16px;
          height:16px;
          border:4px solid #ffffff;
          background-color:#1D1D1D;
          border-radius:100%;
          top:50%;
          transform:translateY(-50%);
		//   right:-73px;
          right:-113px;
          z-index:1000;
        }
        &.big:before {
          width:24px;
          height:24px;
          transform:translate(4px,-50%);
        }
      }
      body {
        color:#aaa;
        p {
          line-height:1.4em;
        }
      }
      &:nth-child(2n) {
        text-align:left;
        float:right;
        .title {
          &:before {
            //left:-63px;
			left:-103px;
          }
          &.big:before {
            transform:translate(-4px,-50%);
          }
        }
      }
    }
  }
}

